<template>
  <a-input v-model:value="url" placeholder="http://......" /><a-button
    type="primary"
    @click="submitURL"
    >添加商品</a-button
  >
  <a-button @click="update">立即更新</a-button>
  <a-table
    :columns="columns"
    :data-source="dataSource"
    :row-key="(record) => record.id"
  >
    <template #bodyCell="{ column, record }">
      <template v-if="column.key === 'img'"
        ><a-image :width="40" :src="record.img" />
      </template>
      <template v-else-if="column.key === 'availble'">{{
        record.availble ? "有货" : "无货"
      }}</template>
      <template v-if="column.dataIndex === 'target_price'">
        <a-input
          v-model:value="record.target_price"
          @pressEnter="changeTarget(record.id, record.target_price)"
        />
      </template>
      <template v-else-if="column.key === 'updated_at'">{{
        record.updated_at.split(".")[0]
      }}</template>
      <template v-else-if="column.dataIndex === 'operation'">
        <a-popconfirm
          v-if="dataSource.length"
          title="Sure to delete?"
          @confirm="onDelete(record.id)"
        >
          <a>Delete</a>
        </a-popconfirm>
      </template>
    </template>
  </a-table>

  <a-modal
    v-model:visible="modalVisible"
    :title="formState.platform + ' ' + formState.name"
    @ok="addArticle"
  >
    <a-form
      ref="formRef"
      :model="formState"
      :label-col="labelCol"
      :wrapper-col="wrapperCol"
    >
      <a-form-item
        label="颜色"
        :rules="[{ required: true, message: 'Please input your 颜色!' }]"
      >
        <a-select v-model:value="formState.color" @change="colorChange">
          <a-select-option
            v-for="item in options"
            :value="item.name"
            :key="item.productID"
            >{{ item.name }}</a-select-option
          >
        </a-select>
      </a-form-item>
      <a-form-item
        label="尺寸"
        :rules="[{ required: true, message: 'Please input your 尺寸!' }]"
      >
        <a-select v-model:value="formState.size" @change="sizeChange">
          <a-select-option
            v-for="item in sizeOptions"
            :key="item.sku"
            :value="item.name"
            >{{ item.name }}</a-select-option
          ></a-select
        >
      </a-form-item>
      <a-form-item
        label="目标价格"
        :rules="[{ required: true, message: 'Please input your targetprice!' }]"
      >
        <a-input-number v-model:value="formState.target_price" />
      </a-form-item>
    </a-form>
  </a-modal>
</template>

<script>
import { ref, defineComponent, onMounted, reactive, toRaw } from "vue";
import { message } from "ant-design-vue";
import axios from "axios";

const columns = [
  {
    title: "图片",
    dataIndex: "img",
    key: "img",
  },
  {
    title: "平台",
    dataIndex: "platform",
    key: "platform",
  },
  {
    title: "名称",
    dataIndex: "name",
    key: "name",
  },
  {
    title: "颜色",
    dataIndex: "color",
    key: "color",
  },
  {
    title: "尺寸",
    dataIndex: "size",
    key: "size",
  },
  {
    title: "有货",
    dataIndex: "availble",
    key: "availble",
  },
  {
    title: "最新价格",
    dataIndex: "latest_price",
    key: "latest_price",
  },
  {
    title: "目标价格",
    dataIndex: "target_price",
    key: "target_price",
  },
  {
    title: "更新时间",
    dataIndex: "updated_at",
    key: "updated_at",
  },
  {
    title: "操作",
    dataIndex: "operation",
  },
];
export default defineComponent({
  setup() {
    const formRef = ref();
    const dataSource = ref([]);
    const modalVisible = ref(false);
    const url = ref();
    const formState = reactive({
      platform: "",
      img: "",
      color: "",
      size: "",
      url: "",
      name: "",
      productId: "",
      skuId: "",
      target_price: "",
    });
    const options = ref([]);
    const sizeOptions = ref([]);
    //const baseUrl = "https://szhsite.herokuapp.com";
    const baseUrl = "http://127.0.0.1:8000";

    const fetch = () => {
      axios
        .get(baseUrl + "/api/articles/")
        .then(function (response) {
          dataSource.value = response.data;
        })
        .catch(function (error) {
          message.error(error);
        });
    };
    function copyObj(obj1, obj2) {
      let key = "";
      for (key in obj1) {
        if (key in obj2) {
          obj2[key] = obj1[key];
        }
      }
    }
    const submitURL = () => {
      axios
        .get(baseUrl + "/api/getchoice/?url=" + url.value)
        .then(function (response) {
          copyObj(response.data, formState);
          options.value = response.data.choice;
          modalVisible.value = true;
        })
        .catch(function (error) {
          console.log(error);
          message.error(error);
        });
    };
    const colorChange = () => {
      formState.size = "";
      formState.skuId = "";
      options.value.forEach(function (item) {
        if (item.name === formState.color) {
          formState.productId = item.productId;
          sizeOptions.value = item.sizes;
        }
      });
    };

    const sizeChange = () => {
      sizeOptions.value.forEach(function (item) {
        if (item.name === formState.size) {
          formState.skuId = item.sku;
        }
      });
    };

    const addArticle = () => {
      axios
        .post(baseUrl + "/api/articles/", toRaw(formState))
        .then(function () {
          message.info("create success");
          fetch();
          modalVisible.value = false;
          url.value = "";
          for (let key in formState) {
            formState[key] = "";
          }
        })
        .catch(function (error) {
          message.error(error);
        });
    };
    const onDelete = (id) => {
      axios
        .delete(baseUrl + "/api/articles/" + id + "/")
        .then(function () {
          message.info("delete");
          fetch();
        })
        .catch(function (error) {
          message.error(error);
        });
    };

    const update = () => {
      axios
        .get(baseUrl + "/api/trigger")
        .then(function () {
          message.info("updating");
        })
        .catch(function (error) {
          message.error(error);
        });
    };

    const changeTarget = (id, target) => {
      axios
        .patch(baseUrl + "/api/articles/" + id + "/", {
          target_price: target,
        })
        .then(function () {
          message.info("target price changed");
        })
        .catch(function (error) {
          message.error(JSON.stringify(error));
          fetch();
        });
    };

    onMounted(() => {
      fetch();
    });

    return {
      formRef,
      url,
      columns,
      dataSource,
      modalVisible,
      formState,
      options,
      sizeOptions,
      sizeChange,
      copyObj,
      submitURL,
      colorChange,
      addArticle,
      onDelete,
      update,
      changeTarget,
    };
  },
});
</script>
